.App {
  text-align: center;
  font-family:sans-serif;
  background-color: #231F20;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Header {
  color:Blue;
  position:fixed;
  z-index: 10;
  top:0;
  left: 0;
  width: 100%;
}



.Footer {
  position: fixed;
  min-width: 100%;
  bottom: 0;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.slopeBegin {
  background-color: #8E793E;
  clip-path: polygon(0 0, 100% 0, 19% 100%, 0 100%);
  
}

.slopeEnd {
  background-color: #AD974F;
  clip-path: polygon(100% 0%, 100% 100%, 19% 100%);
}

.slopeBegin,
.slopeEnd {
  position: absolute;
  width: 100%;
  height: 150%;
  cursor: pointer;
}
